body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*  App-Wide styles for components. Can be overridden by child CSS classes. */

/*  Type-Specific Styling */
input {
  border: 0;
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  box-sizing: border-box;
}

input[type=submit] {
  color: white;
  background-color: darkslategray;

  transition: .25s;
}

input[type=submit]:hover {
  color: white;
  background-color: green;

  transition: .25s;
}

.background-particles {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  z-index: -1337;

  color: grey;
  background-color: slategray;
}

/*  Component-Specific Styling */
.App {
  text-align: center;
  color: white;
}

.App-logo {
  height: 40vmin;
  max-width: 200px;
}

.App-header {
  display: -webkit-flex;
  display: flex;
  min-height: 10vh;

  height: 7.5vh;

  -webkit-flex-direction: column;

          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  font-size: calc(10px + 2vmin);

  background-color: #252525;
  color: rgb(209, 209, 209);
}

.App-body {
  display: grid;
  margin: auto;
  -webkit-align-items: center;
          align-items: center;
  max-width: 400px;

  width: 85vw;
}

.body-text {
  display: grid;
  max-width: 400px;

  width: 85vw;
}

.user-form {
  display: grid;
  max-width: 400px;

  width: 85vw;
}
.background-particles-alt {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  z-index: -1337;
  height: 10000%;
  width: 10000%;
  color: whitesmoke;
  background-color: black; }

.Analysis {
  color: white;
  /*
    opacity: 0;
    animation: initial-screen-slide-down-empty-full ease-out .5s;
    animation-delay: 2s;
    animation-fill-mode: forwards;
    */ }

td {
  padding: 2px;
  border-radius: 1px; }

button[type='sort'] {
  color: white;
  background-color: #636363;
  border-color: black;
  border-radius: 10px;
  margin: 10px;
  transition: .25s; }

button[type='sort'] :active {
  background-color: darkolivegreen;
  transition: .25s; }

.sort-active {
  background-color: green; }

.track-table {
  opacity: 0;
  -webkit-animation: none 0s ease 0s 1 normal none running-screen-slide-down-empty-full ease-out .5s;
          animation: none 0s ease 0s 1 normal none running-screen-slide-down-empty-full ease-out .5s;
  -webkit-animation: initial-screen-slide-down-empty-full ease-out .5s;
          animation: initial-screen-slide-down-empty-full ease-out .5s;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

